
import axios from 'axios'

// const BASE_URL = '/api'
const BASE_URL = 'http://localhost:8000/api'


const getParsedToken = (key=null) => {
    const auth = sessionStorage.getItem("auth")
    if(!key){
        return ( auth !== null ? JSON.parse(auth) : null)
    }
    return ( auth !== null ? JSON.parse(auth)[key] : '')
}

const AnonApi = axios.create({
    baseURL: BASE_URL,
    timeout: 3000,
    headers: {
        'Content-Type': 'application/json',
        'accept': 'application/json'
    }
});

const AuthApi = axios.create({
    baseURL: BASE_URL,
    timeout: 3000,
    headers: {
        'Content-Type': 'application/json',
        'accept': 'application/json'
    }
});

// Custom request intercept
AuthApi.interceptors.request.use((config) => {
    config.headers.common = {
        ...config.headers.common,
        'Authorization': "JWT " + getParsedToken("token"),
    }
    return config
})

// Custom response intercept
AuthApi.interceptors.response.use(
    response => {
        return response
    },
    error => {
      // If our response has an error with Auth API requests.
      const originalRequest = error.config;
      if (error.response.status === 401 && error.response.statusText === "Unauthorized") {
          // If it is a 401 "unauthorized" response, the access token probably expired.
          const {token, orig_iat} = getParsedToken()
          // Lets try to use the refresh token to get a new access token.
          return AuthApi
              .post('/user/refresh-token', {token: token, orig_iat: orig_iat})
              .then((response) => {
                  sessionStorage.setItem('auth', JSON.stringify(response.data));
                  AuthApi.defaults.headers['Authorization'] = "JWT " + response.data.token;
                  originalRequest.headers['Authorization'] = "JWT " + response.data.token;
                  return AuthApi(originalRequest);
              })
              .catch(err => {
                  // If that failed, lets just log the error and remove all the tokens.
                  // Both tokens are invalid so force them to login again.
                  console.log(err);
                  sessionStorage.removeItem('auth');
              });
      }
      return Promise.reject(error);
  }
);


export {
  AnonApi,
  AuthApi
}