import React, { useState } from "react"
import { Link } from 'react-router-dom'
import {AnonApi} from '../Axios'
import { Redirect } from "react-router-dom"
import { Card, Form, Input, Button } from '../components/Auth/AuthForms'


const SignUp = () => {
  const [userName, setUserName] = useState("")
  const [password, setPassword] = useState("")
  const [password2, setPassword2] = useState("")
  const [accountCreated, setAccountCreated] = useState(false)


  const postSignup = (e) => {
    AnonApi.post("/user", {
      username: userName,
      password: password,
      password2: password2
    }).then(result => {
      (result.status === 201 ? setAccountCreated(true) : console.log(result.status))
    }).catch(e => {
      console.log(e)
    })
  }

  if(accountCreated){
    return <Redirect to="/login"/>
  }

  return (
    <div>
      <h1 className="text-center">Sign up</h1>
      <Card>
        <Form>
        <Input
            type="text"
            value={userName}
            onChange={e => setUserName(e.target.value)}
            placeholder="username"
          />
          <Input
            type="password"
            name="password"
            value={password}
            onChange={e => setPassword(e.target.value) }
            placeholder="password"
          />
          <Input 
            type="password"
            name="password2"
            value={password2}
            onChange={e => setPassword2(e.target.value) }
            placeholder="password confirm" />          
          <Button type="submit" onClick={postSignup}>Sign Up</Button>
        </Form>
        <Link to="/login">Already have an account?</Link>
      </Card>
    </div>
  )
}

export default SignUp