import React, {useState} from "react";
import { BrowserRouter as Router, Route} from "react-router-dom";
import AuthRoute from './AuthRoute';
import Homepage from "./pages/Homepage";
import Game from "./pages/Game";
import Login from "./pages/Login";
import SignUp from "./pages/SignUp";
import Profile from "./pages/Profile";
import { Container } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import Navigation from './components/Navigation/Navigation';
import { AuthContext } from "./context/auth";


const App = (props) => {
  const auth = JSON.parse(sessionStorage.getItem("auth"));
  const [authTokens, setAuthTokens] = useState(auth);
  const [isLoggedIn, setIsLoggedIn] = useState(auth !== null);

  const setTokens = (data) => {
    sessionStorage.setItem("auth", JSON.stringify(data))
    setAuthTokens(data)
    setIsLoggedIn(true)
  }
  
  const logOut = (e) => {
    sessionStorage.removeItem("auth");
    setAuthTokens();
    setIsLoggedIn(false);
  }

  return (
    <AuthContext.Provider 
      value={{ authTokens: authTokens, setAuthTokens: setTokens, isLoggedIn: isLoggedIn, setIsLoggedIn: setIsLoggedIn}}>
      <Router>
        <Navigation logOut={logOut}/>
        <Container>
          <AuthRoute exact path="/" component={Homepage} />
          <AuthRoute exact path="/play" component={Game} />
          <AuthRoute exact path="/profile" component={Profile} />
          <Route exact path="/login" component={Login} />
          <Route exact path="/signup" component={SignUp} />
        </Container>
      </Router>
    </AuthContext.Provider>
    
  );
}

export default App;
