import React, { useState } from "react"
import {AuthApi} from '../Axios'
import { Button } from "../components/Auth/AuthForms"
import Card from 'react-bootstrap/Card'

const choices = new Set(['P', 'R', 'S'])
const choice_map = {
  'R': 'Rock',
  'P': 'Paper',
  'S': 'Scissors'
}

const Game = (props) => {
  const [data, setData] = useState({})

  const resetGame = () => {
    setData({})
  }

  const postChoice = (choice) => {
    if(!choices.has(choice)){
      return
    }
    
    AuthApi.post("/games", {
      player_choice: choice
    }).then(result => {
      setData(result.data)
    }).catch(e => {
      console.log("Error: ", e)
    })
  }

  return (
    <div className="text-center">{Object.keys(data).length !== 0 ? 
      <div>
        <h1>Round Results</h1>
        <hr/>
        <h4>You chose: {choice_map[data.player_choice]}</h4>
        <h4>Bot chose: {choice_map[data.bot_choice]}</h4>
        {(data['player_has_won'] === null ? 
          <h2 class="display-2">You tied!</h2>
        :
          <h2 class="display-2">{( data['player_has_won'] ? "You won!" : "You lose!")}</h2>
        )}
        <hr/>
        <Button variant="info" onClick={e => resetGame()}>Play again?</Button>
      </div>
      :
      <div className="row justify-content-md-center">
      <h1 class="display-1">New Game</h1>
      <hr/>
      <h4>Make your choice!</h4>
      
      <Card className="col-md-4" style={{ width: '18rem' }}>
        <Card.Body>
          <Button variant="primary" onClick={e => postChoice('R')}>Rock</Button>
        </Card.Body>
      </Card>

      <Card className="col-md-4" style={{ width: '18rem' }}>
        <Card.Body>
          <Button variant="primary" onClick={e => postChoice('P')}>Paper</Button>
        </Card.Body>
      </Card>

      <Card className="col-md-4" style={{ width: '18rem' }}>
        <Card.Body>
          <Button variant="primary" onClick={e => postChoice('S')}>Scissors</Button>
        </Card.Body>
      </Card>
      </div>
      }
    </div>
  )
}

export default Game