import React, { useState, useEffect}  from "react"
import {AuthApi} from '../Axios'

const Profile = (props) => {
  const [profileData, setProfileData] = useState({})
  
  useEffect(() => {
    AuthApi.get("/user/profile")
      .then(resp =>{
        console.log("PROFILE: ", resp.data)
        setProfileData(resp.data)
      }).catch(data => {
        console.log('PROFILE ERROR: ', data)
      })
  }, [])

  const calc = () => {
    const {total_wins, total_ties, total_games} = profileData
    return ((total_wins + (0.5 * total_ties)) / total_games * 100).toFixed(2)
  }

  return (
    <div className="text-center">
      <h2>{ profileData.username } stats</h2>
      <hr/>
      <h4>Win Percentage</h4>
      {(profileData.total_games ? 
      <p>{calc()}%</p>
      :
      <p>N / A</p>
      )}

      <h4>Total Wins</h4>
      <p>{profileData.total_wins}</p>

      <h4>Total Losses</h4>
      <p>{profileData.total_losses}</p>

      <h4>Total Ties</h4>
      <p>{profileData.total_ties}</p>

    </div>
  )
}

export default Profile