import React, { useState, useEffect}  from "react";
import {AuthApi} from '../Axios'
import ListGroup from 'react-bootstrap/ListGroup'
import Button from 'react-bootstrap/Button'

const Homepage = (props) => {
  const [gameData, setGameData] = useState({})


  useEffect(() => {
    AuthApi.get("/games")
      .then(resp => {
        setGameData(resp.data)
      }).catch(data => {
        console.log(data)
      })
  }, [])

  return (
    <div className="container leaderboard my-2">
      <Button className="col-md-2 mx-auto" variant="primary" onClick={e => props.history.push('/play')}>Play</Button>
      <h2>Overall</h2>
      <ListGroup>
        <ListGroup.Item>Total Games Played: { gameData.games_played }</ListGroup.Item>
        <ListGroup.Item>Total Ties: { gameData.ties }</ListGroup.Item>
        <ListGroup.Item>Total Ties: { gameData.unique_players }</ListGroup.Item>
      </ListGroup>

      <h2>Humans</h2>
      <ListGroup>
        <ListGroup.Item>Wins: { gameData.human_wins }</ListGroup.Item>
        <ListGroup.Item>Losses: { gameData.human_losses }</ListGroup.Item>
        <ListGroup.Item>Rock Plays: { gameData.humans_played_rock }</ListGroup.Item>
        <ListGroup.Item>Paper Plays: { gameData.humans_played_paper }</ListGroup.Item>
        <ListGroup.Item>Scissor Plays: { gameData.humans_played_scissors }</ListGroup.Item>
      </ListGroup>

      <h2>Bots</h2>
      <ListGroup>
        <ListGroup.Item>Wins: { gameData.bot_wins }</ListGroup.Item>
        <ListGroup.Item>Losses: { gameData.bot_losses }</ListGroup.Item>
      </ListGroup>
    </div>
  )
}

export default Homepage